  /* ⬆️⬆️⬆️ SERVICES ⬆️⬆️⬆️ */
  
  /* ⬇️⬇️⬇️ SKILLS ⬇️⬇️⬇️ */
  
  .skills-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .skills-nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100px;
  }
  
  .skills-nav-icon {
    width: 35px;
    transition: 150ms;
  }
  
  .skills-nav-icon:hover {
    width: 50px;
    background-image: linear-gradient(
      to right,
      #25aae1,
      #40e495,
      #30dd8a,
      #2bb673
    );
    box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
    border-radius: 10px;
  }
  
  .skills-nav-btn {
    background-color: transparent;
    border: transparent;
    cursor: pointer;
  }
  
  .skills-description {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-bottom: 100px;
    padding: 20px;
    width: 80%;
    background-color: #3A4750;
    border-radius: 10px;
    box-shadow: 0px 0px 28px 3px #131212;
  }
  
  .skills-description-image {
    width: 1px;
    visibility: hidden;
  }
  
  .skills-description-text {
    width: 100%;
    color: #F4FCFE;
  }
  
  .skills-description-text-title {
    color: #F4FCFE;
    font-size: 20px;
    font-family: 'Michroma', sans-serif;
    margin-bottom: 10px;
  }
  
  .skills-description-text p {
    color: #F4FCFE;
    font-size: 15px;
    text-align: center;
    line-height: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: justify;
  }
  
  .skills-description-text a {
    font-weight: bold;
    text-decoration: none;
    border: none;
  }
  
  .skills-description-text h4 {
    color: #3ac38e;
    font-size: 15px;
    text-align: center;
    line-height: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    cursor: pointer;
  }
  
  .skills-description-text-font {
   
  }
  
  
  .about-description-text {
  
  }
  
  .about-description-text-trybe {
    color: #3ac38e;
    font-weight: bold;
    text-decoration: none;
    border: transparent;
  }
  
  
  /* ⬆️⬆️⬆️ SKILLS ⬆️⬆️⬆️ */
  
  /* ⬇️⬇️⬇️ FOOTER ⬇️⬇️⬇️ */
  
  .footer-background {
    display: flex;
    justify-content:center;
    align-items: center;
    border-top: 1px solid rgba(114, 114, 126, 0.2);
    width: 100%;
    color: #F4FCFE;
  }
  
  .footer-description {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .footer-description p {
    display: flex;
    flex-direction: column;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
    font-size: 12px;
    width: 100%;
    margin:0;
  }
  
  .footer-social-networks {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
  }
  
  .footer-social-networks-icon {
    width: 28px;
    margin: 8px;
    border-radius: 5px;
  }
  
  /* ⬆️⬆️⬆️ FOOTER ⬆️⬆️⬆️ */
}

